.list-circle {
    list-style-type: none;
    padding: 0;
}

.list-circle li {
    position: relative;
    font-size: 16px;
    font-weight: 500;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding-left: 8px;
    margin-bottom: 15px;
}

.list-circle li:last-child {
    margin: 0;
}

.list-circle li:before {
    content: '';
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: rgba(47, 91, 231, 0.8);
    -webkit-box-shadow: 0 0 0 7px rgba(47, 91, 231, 0.1);
    box-shadow: 0 0 0 7px rgba(47, 91, 231, 0.1);
    display: inline-block;
    margin-right: 20px;
    vertical-align: middle;
}

#loading {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 10000;
}

#loading .ripple {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    position: absolute;
    top: calc(50% - 30px);
    left: calc(50% - 30px);
    -webkit-animation-name: ripple;
    animation-name: ripple;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-timing-function: ease-in-out;
    animation-timing-function: ease-in-out;
    animation-direction: alternate-reverse;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    -webkit-transform: scale(0);
    transform: scale(0);
    opacity: 0.6;
}

#loading .ripple1 {
    background-color: rgba(47, 231, 177, 0.4);
}

#loading .ripple2 {
    -webkit-animation-delay: -250ms;
    animation-delay: -250ms;
    background-color: rgba(47, 231, 177, 0.5);
}

#loading .ripple3 {
    -webkit-animation-delay: -500ms;
    animation-delay: -500ms;
    background-color: rgba(47, 91, 231, 0.6);
}

#loading .ripple4 {
    -webkit-animation-delay: -750ms;
    animation-delay: -750ms;
    background-color: #2F5BE7;
}

@-webkit-keyframes ripple {
    to {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

@keyframes ripple {
    to {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

